*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --text-color-primary: #4f4141;
  --background-color: #fff;
  --text-header-text-color: #053a31;
  --text-header-name-color: #8635a4;
  --text-header-name-color-accent: #5c3462;
  --text-header-description-color: #835a4a;
  --text-footer-nav-color: #8635a4;
  --border-footer-style: 1px solid #928484;
  --icon-socials-footer-fill: gray;
  --icon-socials-footer-fill-accent: blue;
  --text-footer-copyright-color: #928484;
  --books-page-bcg: #fff9b6;
  --books-item-bcg: white;
  --books-item-border-color: #000;
  --books-caption-bcg: #fff36b;
  --books-title-color-accent: #09792b;
  --animation-base: 250ms cubic-bezier(.4, 0, .2, 1);
  --animation-color: color 250ms cubic-bezier(.4, 0, .2, 1);
  --animation-bcg: background-color 250ms cubic-bezier(.4, 0, .2, 1);
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

html {
  scroll-behavior: smooth;
}

:before, :after {
  box-sizing: border-box;
}

body {
  letter-spacing: .04em;
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizespeed;
  height: 100%;
  color: var(--text-color-primary);
  background-color: var(--background-color);
  font-family: Roboto, Verdana, sans-serif;
